*{
  margin:0;
  box-sizing:border-box;
}

html{
  width:100vw;
  min-height:100vh;
}

body {
  font: 14px "Century Gothic", Futura, sans-serif;
  background: #000;
  color:#fff;
  display:flex;
  justify-content:center;
  align-items:center;
  min-height:100vh;
}
h1{
  margin:40px;
  text-align:center;

}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 2px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 50px;
  height: 50px;
  cursor:pointer;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  justify-content:center;
  align-items:flex-start;
  flex-wrap:wrap;
  width:100%;
  max-width:500px;
  min-height:400px;
}

.game-board{
  display:flex;
  justify-content: center;
  flex-direction: column;
}

.game-info {
  margin-left: 20px;
  overflow:auto;
  min-width:320px;
  min-height:180px;
}

.game-status{
  margin:0 5px 10px 20px;
  font-size:1.5rem;
}

.historyBtn{
  width:120px;
  height:30px;
}

.activeHistoryBtn{
  width:130px;
  height:30px;
  font-weight:bold;
} 

.replayBtn{
  margin:20px auto;
  width:100px;
  height:40px;
  border:1px solid #fff;
  border-radius:5px;
  background-color: #2af;
  outline:none;
  cursor:pointer;
  font-size:1rem;
}

@media screen and (max-width:640px){
  .game-info{
    margin-top:20px;
    margin-left: 0;
  }
}